@import url('https://fonts.googleapis.com/css2?family=Kalam&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500&display=swap');

.app{
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main{
  width: 80vw;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

p{
  color: #06130f;
  font-family: "Kalam", cursive;
  font-size: 6vmin;
}
p em{
  font-weight: bold;
  color: #FFF1FA;
}

.boardTitle{
  color: #000000;
  font-family: "Kalam", cursive;
  font-size: min(5vw, 4rem);

  width: min(35vw, 33rem);
  height: min(4rem, 5vw);
  
  background: #99714a;

  border-style: solid solid none solid;
  border-color:#99714a;
  border-width: min(1.5rem, 2vw);
  border-radius: min(1.5vw, 1rem) min(1.5vw, 1rem) 0vw 0vw;

  line-height: min(5vw, 4rem);
  text-align: center;
}
.board{
  display: grid;
  grid-template-columns: repeat(4, min(20vw, 16rem));
  grid-auto-rows: min(25vw, 20rem);
  gap: min(1.25vw, 1rem);
  background-color: #C4A484;
  padding: min(3.75vw, 3rem);

  border-style: solid;
  border-color:#99714a;
  border-width: min(1.875vw, 1.5rem);
  border-radius: min(1.25vw, 1rem);
}
@media only screen and (max-width: 600px) {
  .board{
    grid-template-columns: repeat(3, 20vw);
  }
  .main{
    height: 95vh;
  }
}
img{
  width: 100%;
  height: 100%;
}


img:nth-child(2n) {transform: rotate(-2deg); }
img:nth-child(3n) {transform: rotate(6deg); }
img:nth-child(4n) {transform: rotate(-8deg); }
img:nth-child(1n) {transform: rotate(8deg); }
img:nth-child(5n) {transform: rotate(-6deg); }
img:nth-child(6n) {transform: rotate(10deg); }
img:nth-child(7n) {transform: rotate(8deg); }
img:nth-child(8n) {transform: rotate(-8deg); }
.body{
  border: none;
  grid-column: span 2;
  display: grid;
  grid-template-columns: repeat(3, calc(min(40vw, 32rem)/3));
  grid-template-rows: min(6.25vw, 5rem) min(17.5vw, 14rem);
  gap: min(0.625vw, 0.5rem);
}
.timeTitle{
  grid-column: span 3;

  color: #06130f;
  font-family: "Kalam", cursive;
  font-size: min(3.75vw, 3rem);

  text-align: center;
}
.timeUnit{
  background: white;

  border-style: solid;
  border-color: white;
  border-width: min(0.625vw, 0.5rem);
  border-radius: min(0.5vw, 0.4rem);

  text-align: center;
  vertical-align: middle;

  font-family: "Kalam", cursive;
  line-height: min(5vw, 4rem);
  font-size: min(2.25vw, 2rem);
}
.timeNum{
  border: none;
  background: lightgray;
  width: calc(min(36.25vw, 29rem)/3);
  height: calc(min(36.25vw, 29rem)/3);
  line-height: calc(min(36.25vw, 29rem)/3);

  border-color: lightgray;
  border-radius: min(0.25vw, 0.2rem);
  
  font-size: min(6.25vw, 5rem);
  font-family: 'Quicksand', sans-serif;
  font-weight: 500;
}